@use "../helper" as *;

/* Block "page-head" */
.page-head {
  padding: 70px 0 0 0;

  &_only-mobile {
    display: block;

    @include for-min-size("lg") {
      display: none;
    }
  }
  &_only-desctop {
    display: none;

    @include for-min-size("lg") {
      display: block;
    }
  }

  // .page-head__inner
  &__inner {
  }

  // .page-head__bread-crumbs
  &__bread-crumbs {
    padding: 0 0 14px 0;
  }

  // .page-head__title

  &__title {
    text-align: center;

    @include for-min-size("md") {
      text-align: left;
    }
  }
}
